'use strict';

let optionsList =  ["Saab", "Volvo", "BMW"]

function custom_select(){
  const customSelectList = document.getElementsByClassName("custom-select"); //get een list of all custom-selects
  for (let index = 0; index < customSelectList.length; index++) {
    const customSelect = customSelectList[index].getElementsByTagName("select")[0]; // get all options.
    console.log("num of options ",customSelect.length,customSelect)
    let newSelectDiv = document.createElement("DIV")
    newSelectDiv.setAttribute("class","select-styling")


    for (let index = 0; index < customSelect.length; index++) {
      const value = customSelect.options[index].value
      const text = customSelect.options[index].innerHTML
      console.log(value,text)
      let newOptionDiv = document.createElement("DIV")
      newOptionDiv.innerHTML = text;
      newOptionDiv.setAttribute("class","options-styling")
      newOptionDiv.setAttribute("value",index)
      newSelectDiv.setAttribute("class","options-hide")

      newOptionDiv.addEventListener("click",function(e){
        console.log(this.innerHTML)

      })


      newSelectDiv.appendChild(newOptionDiv)
  
    }
    customSelectList[index].appendChild(newSelectDiv );               // Append <button> to <body>

   
   
 

    
  }



}

custom_select()


